<template>
  <div class="hello">
    <el-container style="height: 100%; border: 0px solid #eee">
      <el-aside width="350px" style="position: fixed">
        <clock />
        <el-row>
        </el-row>
      </el-aside>

      <el-container>
        <el-main>

          <div class="detail-container">

            <div class="filter-container">
              <el-input
                v-model="searchWord"
                placeholder=""
                style="width: 300px"
                class="filter-item"
                @clear="handleFilter"
                @keyup.enter.native="handleFilter"
                @click="handleFilter"
              />

              <el-button
                v-waves
                class="filter-item"
                type="primary"
                icon="el-icon-search"
                style="margin-left: 10px"
                @click="handleFilter"
              >查询
              </el-button>
            </div>

            <br />
            <br />
            <el-table
              :key="tableKey"
              v-loading="listLoading"
              :data="list"
              border
              fit
              highlight-current-row
              :show-header="false"
              style="width: 90%; height: 80%; margin:0 auto; line-height: 17px;"
            >
              <el-table-column
                label="ID"
                prop="id"
                align="center"
                width=""
                min-width="20"
              />
              <el-table-column label="标题" prop="title" width="" min-width="150" align="center" />
              <el-table-column label="日期" prop="date" width="" min-width="30" align="center" />
              <el-table-column
                label="操作"
                width=""
                min-width="30"

                align="center"
              >
                <template slot-scope="{ row }">
                  <a style="cursor: pointer; color: #A020F0; line-height: 17px;" @click="readDetail(row)">detail...</a>
                </template>
              </el-table-column>
            </el-table>
            <h1></h1>
            <el-pagination
              background
              layout="prev, pager, next"
              :total="total"
              :current-page.sync="listQuery.page"
              :page-size="10"
              @current-change="handleCurrentChange"
              >
            </el-pagination>
          </div>


        </el-main>
      </el-container>
    </el-container>
  </div>
</template>



<script>
import Clock from './Clock';
import Paper from './Paper';
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "clock": Clock,
        "paper": Paper
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: "abcd",
      dialogVisible: false,

      tableKey: 0,
      listLoading: true,
      list: [],
      total: 0,

      listQuery: {},
      showCover: false,
      categoryList: [],

      directoryList: [],
      searchWord: ''

    }
  },
  mounted() {
    this.getDict()
    this.handleFilter(1, 10)
  },
  methods: {
    readDetail(row) {
      this.$router.push(`/markdown/${row.id}`)
    },
    handleFilter(pageNum, pageSize) {
      pageNum = this.listQuery.page
      pageSize = 10
      if(pageNum == NaN) {
        pageNum = 1;
        pageSize = 10;
      }
      var searchWord = this.searchWord

      // 更具搜索词过滤
      var directoryList = new Array()
      for(var i = this.directoryList.length - 1; i >= 0; i--) {
          console.log(this.directoryList[i][1])
          console.log(this.directoryList[i][1].search(searchWord))
          if(this.directoryList[i][1].search(searchWord) > -1) {
            directoryList.push(this.directoryList[i])
          }
      }

      var start = (pageNum - 1) * pageSize
      var end = start + pageSize - 1

      var list = new Array()
      if(end >= directoryList.length - 1) {
        end = directoryList.length - 1
      }


      for(var i = start; i <= end; i++) {
        list.push({id: directoryList[i][0], title: directoryList[i][1], date: directoryList[i][2] })
      }
      const currentCount = list.length

      list.sort(
        (pre, cur) =>
          cur.id - pre.id
      )
      this.list = list
      this.total = directoryList.length
      this.listLoading = false
    },
    async getDict() {
      // 同步请求
      const xhr = new XMLHttpRequest();
      xhr.open('GET', '/static/knowledge/directory.txt', false);
      xhr.send(null);

      if(xhr.readyState === 4 && xhr.status === 200) {
        console.log(xhr.responseText);

        var list = xhr.responseText.split("\n")
        for(var i = 0; i <= list.length - 2; i++) {
            var arr = [list[i].split(",")[0], list[i].split(",")[1], list[i].split(",")[2]]
            this.directoryList.push(arr)
        }
      }
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.handleFilter(val, 10);
    },
    parseQuery() {
      const listQuery = {
        page: 1,
        pageSize: 10,
        sort: '+id'
      }
      this.listQuery = { ...listQuery, ...this.listQuery }
    },
    detail(row) {
      alert(row.id)
    }
  }
}
</script>

<style scoped>
  .hello {
    height: 95%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc, #54FF9F, #1E90FF, #A020F0);

  }

  .el-aside {
    border-color: blue;
  }

  .el-container {
    height: 100%;
  }

  .el-main {
    overflow-y:auto;
    margin-top: 0px;
    margin-left: 350px;
    height: 100%;
  }

  .el-row {
    margin-top: 10px;
  }

  .el-button {
    width: 100px;
    margin-left: 10px;
  }

  .detail-container {
    border: 0px solid blue;
    border-radius: 30px;
    width: 800px;
  }

  .el-table {
      border: 0px solid blue;
      border-radius: 25px;
      width: 800px;
      row-height: 6px;

  }

  .el-input {
    border-radius: 10px;
  }


</style>



